// ============================================================================
// 踏月寻仙 - MVU 变量结构定义 (Zod Schema for MVU Variables)
// ============================================================================
// 修炼境界等级与境界描述映射
// 等级 1-4:   练气（初期、中期、后期、大圆满）
// 等级 5-8:   筑基（初期、中期、后期、大圆满）
// 等级 9-12:  金丹（初期、中期、后期、大圆满）
// 等级 13-16: 元婴（初期、中期、后期、大圆满）
// 等级 17-20: 化神（初期、中期、后期、大圆满）
// 等级 21-24: 炼虚（初期、中期、后期、大圆满）
// 等级 25-28: 合体（初期、中期、后期、大圆满）
// 等级 29-32: 大乘（初期、中期、后期、大圆满）
// 等级 33-36: 渡劫（初期、中期、后期、大圆满）
const REALM_NAMES = ['练气', '筑基', '金丹', '元婴', '化神', '炼虚', '合体', '大乘', '渡劫'];
const REALM_STAGES = ['初期', '中期', '后期', '大圆满'];
// 修炼境界等级与突破阈值映射
const REALM_THRESHOLDS = [100, 100, 100, 100, 200, 200, 200, 200, 400, 400, 400, 400, 800, 800, 800, 800, 1600, 1600, 1600, 1600, 3200, 3200, 3200, 3200, 6400, 6400, 6400, 6400, 12800, 12800, 12800, 12800, 25600, 25600, 25600, 25600];
// 修炼境界等级与寿元上限映射
const REALM_LIFESPANS = [100, 100, 100, 100, 200, 200, 200, 200, 500, 500, 500, 500, 1000, 1000, 1000, 1000, 2000, 2000, 2000, 2000, 5000, 5000, 5000, 5000, 10000, 10000, 10000, 10000, 50000, 50000, 50000, 50000, 100000, 100000, 100000, 100000];
// 物品 Schema
const ItemSchema = z.object({
    名称: z.string().prefault(''),
    描述: z.string().prefault(''),
    品阶: z.string().prefault(''),
    数量: z.coerce.number().transform(v => Math.max(0, v)).prefault(1), // 允许数量为 0，由后续 transform 过滤
});
// 宗门势力 Schema - 优化版
const FactionSchema = z.object({
    地: z.string().describe('所在地'),
    特: z.string().describe('核心特点'),
    力: z.string().describe('最高战力'),
    营: z.enum(['正', '魔', '中']),
    模: z.enum(['超大', '大', '小', '微', '特']),
});
// 功法 Schema - 优化版
const TechniqueSchema = z.object({
    阶: z.enum(['凡', '黄', '玄', '地', '天', '仙', '圣']),
    性: z.string().describe('属性'),
    效: z.string().describe('效果'),
});
// 法宝兵器 Schema - 极简版
const TreasureSchema = z.object({
    阶: z.enum(['凡器', '法器', '灵器', '法宝', '灵宝', '仙器', '圣器', '道器']),
    类: z.string().describe('类型'),
}).transform(data => ({
    ...data,
    特: data.阶 === '仙器' ? '顶级' : data.阶 === '灵宝' ? '强' : data.阶 === '法宝' ? '中' : '普通',
}));
// 地点 Schema - 优化版
const LocationSchema = z.object({
    域: z.enum(['天层', '神州', '东苍', '南炎', '西庚', '北冥', '下层', '四海']),
    类: z.enum(['秘境', '城镇', '宗门', '禁地', '遗迹', '地形']),
    危: z.coerce.number().transform(v => _.clamp(v, 0, 100)),
    特: z.string().describe('特点'),
    资: z.array(z.string()).prefault([]),
});
// 灵根 Schema - 极简版（速度根据质自动计算）
const SpiritRootSchema = z.object({
    质: z.enum(['劣', '下', '中', '上', '极', '天', '异']),
    性: z.string().describe('属性'),
    稀: z.enum(['常', '少', '罕', '稀', '传']),
}).transform(data => {
    const 速度映射 = { 劣: '0.3倍', 下: '0.5倍', 中: '1倍', 上: '2倍', 极: '3倍', 天: '5倍', 异: '4倍' };
    return {
        ...data,
        速: 速度映射[data.质] || '1倍',
        特: data.质 === '天' ? '单系顶级' : data.质 === '异' ? '变异稀有' : data.质 === '极' ? '双系优秀' : '常规',
    };
});
// 体质 Schema - 优化版
const PhysiqueSchema = z.object({
    质: z.enum(['凡', '灵', '道', '圣', '神']),
    特: z.string().describe('特性'),
    稀: z.enum(['常', '少', '罕', '稀', '传']),
}).transform(data => ({
    ...data,
    优: data.质 === '神' ? '至高' : data.质 === '圣' ? '极强' : data.质 === '道' ? '强' : data.质 === '灵' ? '中' : '无',
}));
// 完整的 MVU 变量结构 - 完全参考角色卡示例的结构
